.area-img {
    width: 300px !important;
    border-radius: 150px;
    border-style: solid;
    border-width: 5px;
    -webkit-box-shadow: 3px 3px 15px black;
            box-shadow: 3px 3px 15px black;
    border-color: #0eae9b !important;
}

.area-img:hover {
    border-color: #0eae9b !important;
    border-radius: 150px;
    -webkit-box-shadow: 6px 6px 15px black;
            box-shadow: 6px 6px 15px black;
}

.area-selector:hover {
    background-color: #0eae9b !important;
    color: #0eae9b;
}


